.product{
    /* display: flex;
    flex-direction: column;
    align-items: center;
    margin: 10px;
    padding: 20px;
    width:100%;
    max-height: 400px;
    min-width: 100px;
    background-color: white;
    z-index: 1;
    justify-content: flex-end; */
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(71 71 71);
    padding: 20px;
    margin: 20px;
    flex-direction: column;
    justify-content: space-around;
    background: white;
    align-items: center;
    min-width: 40%;
    max-width: 30%;
    text-align: center;
}
.product > img{
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin: 15px 0 20px 0
}
.product > button{
    background: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
    cursor: pointer;
    padding: 5px 10px;
    border: none;
    outline: none;
    border-radius: 5px;
}
.product__price{
    margin-top: 5px;
}
.product__info{
    margin-bottom: 10px;
}
.product__category{
    font-size: 14px;
    margin-top: 5px;
    color: gray;
}
.product:hover{
    transform: scale(1.05);
    box-shadow: 10px 10px 12px lightgray;
}
.product>button:hover{
    background: #f3be39;
}
